import React, { Component } from "react";
import { Link } from "react-router-dom";
import API from "../../utils/API";
import GoogleLogin from 'react-google-login';
import { GoogleLogout } from 'react-google-login';
import logout from "./logout.js";
import CSS from "./Profile.css";

class Profile extends Component {
  state = {
    userName: "W Frankwich",
    colorSchemes: []
  };
  
  componentDidMount() {
    API.getColorSchemes()
      .then(res => {
        console.log(res.status);
        this.setState({ colorSchemes: res.data })
      })
      .catch(err => console.log(err));
  };

  consoleLogging = () => {
    console.log(this.state.colorSchemes);
  };

  render() {
    return (
      <div>
        <div className="jumbotron profile-jumbo"><br /><br />
        <h2>{this.state.userName}</h2>
        <button onClick={this.consoleLogging}>hiya</button>
        {/* <ul>
        {this.state.colorSchemes.map(scheme => (
          <li 
            key={scheme._id}
          >
            <strong>
              {scheme.title}
            </strong>
          </li>
        ))}
        </ul> */}
        </div>
        <a href="#" className="login">
        <GoogleLogout
            className="login-button inline"
            buttonText="Logout"
            onLogoutSuccess={logout}
        >
        </GoogleLogout>
        </a>
      </div>
    );
  }
}

export default Profile;


